// sass vars

$main-grey: #e0e0e0;

// propriétées générales

html {
    height: 100%;
    margin: 0; }

body {
    margin: 0; }

body,
#root,
.App {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0; }

// layout global

// transformation de app en
// - grid pour gérer plus facilement la
// - superposition pour la popup
.App {
    display: grid;
    grid-template-rows: 100%;
    grid-template-columns: 100%;

    #main-container {
        grid-row: 1 / 2;
        grid-column: 1 / 2;
        margin: 8px auto;
        max-width: 850px; } }

#login-popup-container {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    z-index: 2;
    background-color: #00000031;

    display: flex;
    justify-content: space-around;

    form { // only element in this popup container
        background-color: #ffffff;
        height: fit-content;
        margin-top: 8%;
        padding: 15px;
        border-radius: 2%;
        max-width: 250px;

        input,
        textarea,
        button,
        select {
            display: block;
            margin-bottom: 5px; }

        textarea {
            height: 110px;
            resize: none; }

        textarea {
            min-width: 201px; }
        input[type=text] {
            min-width: 200px; }
        input[type=date] {
            min-width: 202px; }
        input[type=password] {
            min-width: 200px; } } }

