// header
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #e6e6e6;
    margin-bottom: 15px;
    padding: 5px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;

    h1 {
        margin-left: 5%; }

    .MuiButton-root {
        min-height: 30px;
        margin-right: 20px;
        margin-left: 10px; } }

// conteneur des colonnes de tâches
#schedule-container {
    // flex
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    // props
    margin: 0px 5px;
    padding-right: 5px;
    width: fit-content;

    .schedule-column {
        width: 120px;
        margin: 5px 0px 5px 5px;
        padding: 5px 5px;

        .schedule-column-header {
            background-color: $main-grey;
            margin: 0px 2px;
            padding: 2px 2px;
            text-align: center;
            user-select: none;
            cursor: pointer;
            box-shadow: -1px 1px #b3b3b3;
            border-radius: 5%;

            &.collapsed {
                opacity: .5; } }

        .schedule-item {
            border-radius: 3%;
            padding: 5px 5px;
            margin-top: 5px;
            cursor: pointer;

            &.done {
                background-color: grey;
                // text-decoration: line-through
                opacity: .5;

                &:hover {
                    opacity: .85; } } } } }

// navigateur (changer semaine, avance, reculer, etc)
#schedule-navigator {
    margin-bottom: 15px;
    &>* {
        margin: 0px 5px;
        height: 30px; } }

@media screen and (max-width: 540px) {
    #schedule-navigator {
        margin-bottom: 15px;

        display: flex;
        flex-direction: column;
        &>* {
            margin: 5px 5px;
            height: 30px; } } }

// color selector

.color-selector {
    display: flex;
    margin: 5px 5px;

    .color-box {
        width: 20px;
        height: 20px;
        margin: 2px 2px;
        cursor: pointer;

        &.color-selected {
            box-sizing: border-box;
            -moz-box-sizing: border-box;
            -webkit-box-sizing: border-box;
            border: 1px black solid; } } }
